<template>
	<view class="">
		<view class="black_wrap">
			<view class="type_serve pad-30">
				<view class="flex-lr close_btn">
					<text></text>
					<image src="../static/img/icon_close.png" class="icon_close" @click="cancel"></image>
				</view>
				<view class="flex-lr pad-tb-20 border_bottom" v-if="!disabled || postData.type==10">
					<view class="flex-center" @click="changeTypeCarry(10)">
						<image v-if="postData.type == 10" src="../static/img/icon_radio_check.png" class="icon_radio"></image>
						<image v-else src="../static/img/icon_radio.png" class="icon_radio"></image>
						<text class="f-36 color-1 mar-l-20">带人</text>
					</view>
					<view class="flex-center">
						<view class="count_btn" @click="changePeople(1)">-</view>
						<input type="number" v-model="postData.people_number" class="count_person" />
						<view class="count_btn" @click="changePeople(2)">+</view>
						<text class="color-3 f-26 mar-l-10">人</text>
					</view>
				</view>
				<view class="mar-t-20" v-if="!disabled || postData.type==20">
					<view class="flex-center" @click="changeTypeCarry(20)">
						<image v-if="postData.type == 20" src="../static/img/icon_radio_check.png" class="icon_radio"></image>
						<image v-else src="../static/img/icon_radio.png" class="icon_radio"></image>
						<text class="f-36 color-1 mar-l-20">带物</text>
					</view>
					<view class="mar-t-30">
						<view class="color-6 f-30">物品类型</view>
						<view class="list_option">
							<text @click="setMatter(el.title)" :class="postData.matter_type == el.title?'on':''" v-for="el in matterTypeList"
							 :key='el.id'>{{el.title}}</text>
						</view>
					</view>
					<view class="mar-t-30">
						<view class="color-6 f-30">物品重量</view>
						<view class="list_option">
							<text @click="serWeight(el.title)" :class="postData.matter_weight == el.title?'on':''" v-for="el in weightList"
							 :key='el.id'>{{el.title}}</text>
						</view>
					</view>
				</view>
				<view class="mar-t-60">
					<button type="primary" class="bg-blue" @click="sureServeType()">确定</button>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import ReleaseApi from '../apis/release.js'
	export default {
		name: 'matter_layer',
		data() {
			return {
				postData:{
					type:'',
					people_number: 0, //人数
					matter_type: '', //物品类型
					matter_weight: '', //物品重量
				},
				weightList: [],
				matterTypeList: []
			}
		},
		props: ['type','people_number','matter_weight','matter_type','disabled'],
		mounted() {
			this.getOption()
			this.init()
		},
	
		methods: {
			init(){
				this.postData = {
					type:this.type,
					people_number: this.people_number, //人数
					matter_type: this.matter_type, //物品类型
					matter_weight: this.matter_weight,//物品重量
				}
			},
			cancel() {
				this.$emit('change')
			},
			getOption() {
				ReleaseApi.option().then(res => {
					this.weightList = res.weight
					this.matterTypeList = res.stdmode
				})
			},
			changePeople(type) {
				let val = type == 1 ? this.postData.people_number - 1 : this.postData.people_number + 1
				if (val < 0) {
					return
				}
				this.postData.people_number = val
			},
			setMatter(val) {
				this.postData.matter_type = val
			},
			serWeight(val) {
				this.postData.matter_weight = val
			},
			sureServeType() {
				if (!this.postData.type) {
					this.$util.showToast('请选择载人或载物')
					return
				}
				if (this.postData.type == 10 && this.postData.people_number == 0) {
					this.$util.showToast('请输入载人数量')
					return
				}
				if (this.postData.type == 20 && this.postData.matter_type == '') {
					this.$util.showToast('请选择物品类型')
					return
				}
				if (this.postData.type == 20 && this.postData.matter_weight == '') {
					this.$util.showToast('请选择物品重量')
					return
				}
				this.$emit('change','SURE',this.postData)
			},
			changeTypeCarry(type){
			    if(this.disabled){
					return
				}
				this.postData.type = type
			},
		}
	}
</script>

<style lang="less" scoped>
	.type_serve {
		position: absolute;
		left: 0;
		right: 0;
		bottom: 0;
		background: #FFFFFF;
		border-radius: 20rpx 20rpx 0rpx 0rpx;
		padding-top: 0;
		min-height: 368rpx;

	}
	.close_btn {
		padding: 15rpx 0;
		border-bottom: #E6E6E6 solid 1rpx;
	}

	.btn {
		margin-top: 30rpx;
	}

	.icon_radio {
		width: 38rpx;
		height: 38rpx;
	}

	.count_btn {
		width: 36rpx;
		height: 36rpx;
		background: #F2F2F2;
		border-radius: 15rpx;
		line-height: 28rpx;
		text-align: center;
		font-size: 28rpx;
		font-weight: 800;
		color: #333333;
	}

	.count_person {
		width: 50rpx;
		text-align: center;
		margin: 0 10rpx;
	}

	.list_option {
		font-size: 0;
		margin-top: 10rpx;

		text {
			width: 130rpx;
			height: 50rpx;
			border: 1rpx solid #D9D9D9;
			border-radius: 8rpx;
			font-size: 24rpx;
			font-weight: 500;
			color: #1A1A1A;
			text-align: center;
			line-height: 48rpx;
			margin-right: 10rpx;
			margin-top: 10rpx;
			display: inline-block;
			vertical-align: top;

			&.on {
				border-color: #0493FF;
				color: #0493FF;
			}
		}
	}
</style>
